<template>
  <div>
    
    <template>
      <v-simple-table>
        <template v-slot:default>
          <tbody>
            <tr v-for="member in memberlist" :key="member.userId">
              <template>
                <v-card class="mx-auto" max-width="300">
                  <v-img :src="url + member.profileImg" height="200px"></v-img>
                  <v-card-title>
                    {{member.userName}}({{ member.userId }})  : {{ member.nickName }}
                  </v-card-title>

                  <v-card-subtitle>
                    <v-btn @click="rejectjoin(member.userNo)"></v-btn>
                  </v-card-subtitle>

                  <v-card-actions>
                    <v-btn color="orange lighten-2" text></v-btn>
                    <v-spacer></v-spacer>
                    <v-btn icon @click="show = !show">
                      <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                    </v-btn>
                  </v-card-actions>

                  <v-expand-transition>
                    <div v-show="show">
                      <v-divider></v-divider>

                      <v-card-text>
                          : 10
                          : 40
                      </v-card-text>
                    </div>
                  </v-expand-transition>
                </v-card>
              </template>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </template>
    <router-view></router-view>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data(){
    return{
      classInfo : [],
      memberlist : [],
      show: false,
    }
  },
  created(){
    this.get_member()
  },
  methods :{
    get_member(){
      axios.get(`class/oneclass/`+this.$route.params.classno)
        .then((Response)=>{
            this.classInfo = Response.data
            if(this.classInfo.userNo == this.userInfo.userNo){ //
                axios.get(`class/memberlist/`+this.$route.params.classno) //  
                .then((res)=>{
                    this.memberlist = res.data
                    console.dir(res.data)
                })
                .catch((err)=>{
                    err
                })
            }
        })
        .catch((error)=>{
            error
        })
    },
    rejectjoin(userno){
        let data = {
                classNo : this.$route.params.classno,
                studentNo : userno,
                isApproved : 0,
            }
            axios.put('class/classjoin',data)
            .then((Response)=>{
                Response
                alert(".");
                this.$router.go()
            })
        }
  },
  computed : {
    userInfo(){
      return this.$store.getters.userInfo
    },
    url(){
      return this.$store.getters.BACKEND_URL
    }
  }
}
</script>

